import { Alert, Badge, Container, Row, Col, Button } from "react-bootstrap";

const CompetitionSummary = ({ title, summary }) => {
    return (
        <Container>
            <Row>
                <Alert className="text-right w-100" variant="info">
                    <Alert.Heading className="text-center">
                        <Badge variant="success p-2" >
                            {title}
                        </Badge>
                    </Alert.Heading>

                    <p>{summary}</p>
                    <p>{summary}</p>
                    <p>{summary}</p>
                    <p>{summary}</p>
                </Alert>
            </Row>
            <hr />
            <Row>
                <Alert className="text-right w-100" pill="true" variant="info">
                    <p>   : 00000000</p>
                    <p> </p>
                </Alert>
            </Row>
            <hr />
            <Row>
                <Col>
                    <Button block variant="outline-success">  </Button>
                </Col>
                <Col>
                <Button block variant="outline-secondary"> </Button>
                </Col>
            </Row>
        </Container>
    );
};

export default CompetitionSummary;
