import React, { Component } from "react";
import ModalSignIn from "./ModalSignIn";
import "./users.css";
import userServices from "../services/http/userServices";
import { withRouter } from "react-router-dom";
import LoadingBar from "../commons/LoadingBar";
import Configs, { browserStorage } from "../services/configs";
import { Card, Form, Button } from "react-bootstrap";
import { Sorry, OK, Attention } from "../tools/notification";

class SignUp extends Component {
	// *********************Objectives***********************
	// 1. handle errors particularly
	// 2. first and last name must be persian text so make the direction correct and force persian chars
	state = {
		studentID: "",
		password: "",
		confirmPassword: "",
		firstname: "",
		lastname: "",
		email: "",
		loading: false,
	};

	checkConfirmPassword = (event) => {
		this.setState({ confirmPassword: event.target.value });
		if (this.state.password !== event.target.value) {
			event.target.setCustomValidity(
				"           "
			);
			//console.log(password, confirmPassword);
		} else event.target.setCustomValidity("");
	};
	onSignUpSubmit = async (event) => {
		event.preventDefault();
		const {
			studentID,
			password,
			confirmPassword,
			firstname,
			lastname,
			email,
		} = this.state;
		if (firstname.trim().length < 3 || lastname.trim().length < 3) {
			Sorry("          ");
			this.setState({
				firstname: firstname.trim(),
				lastname: lastname.trim(),
			});
			return;
		}
		if (password === confirmPassword) {
			try {
				this.setState({ loading: true });
				const newUser = {
					studentID: Number(studentID),
					password,
					email,
					fullname: (firstname + " " + lastname).replace(/\s+/g, " "), // correct? (iterative?)
				};

				const { status, data } = await userServices.signUp(newUser);

				if (status === Configs.Status.CreatedSuccessfully) {
					//console.log(data);
					OK("     ");
					browserStorage.save(data.token);
					this.props.history.replace("/");
					// this.props.history.replace('/signIn')
					// ******* change server to return user token and auto sign in
				}
			} catch (err) {
				// console.log(err);
				this.setState({ loading: false });
				if (err.response.status === Configs.Status.Conflict) {
					Attention(
						"                  "
					);
				} else if (!Configs.Status.isErrorExpected(err)) {
					Sorry(
						"       .    "
					);
				}
			}
		} else {
			Sorry("   ");
		}
		this.setState({ loading: false });
	};

	render() {
		const {
			studentID,
			password,
			confirmPassword,
			firstname,
			lastname,
			email,
			loading,
		} = this.state;

		return (
			<Card border="success" className="sign-up-card">
				<LoadingBar loading={loading} />
				<Card.Header className="bg-transparent text-center border-success">
					  
				</Card.Header>
				<Card.Body>
					<Form onSubmit={(event) => this.onSignUpSubmit(event)}>
						<Form.Group className="form-inline">
							<Form.Label className="w-25"></Form.Label>
							<Form.Control
								type="text"
								pattern="[- ]{3,}" // persian characters and space
								onInput={(e) => e.target.setCustomValidity("")}
								onInvalid={(e) =>
									e.target.setCustomValidity(
										"           "
									)
								}
								className="sign-up-textbox w-75"
								placeholder="First Name"
								value={firstname}
								required="required"
								onChange={(e) =>
									this.setState({
										firstname: e.target.value,
									})
								}
							/>
						</Form.Group>

						<Form.Group className="form-inline">
							<Form.Label className="w-25">
								 
							</Form.Label>
							<Form.Control
								type="text"
								className="sign-up-textbox w-75"
								pattern="[- ]{3,}" // persian characters and space
								onInput={(e) => e.target.setCustomValidity("")}
								onInvalid={(e) =>
									e.target.setCustomValidity(
										"            "
									)
								}
								placeholder="Last Name"
								value={lastname}
								required="required"
								onChange={(e) =>
									this.setState({
										lastname: e.target.value,
									})
								}
							/>
						</Form.Group>

						<Form.Group className="form-inline">
							<Form.Label className="w-25">
								 
							</Form.Label>
							<Form.Control
								type="text"
								pattern="[0-9]{8}"
								onInput={(e) => e.target.setCustomValidity("")}
								onInvalid={(e) =>
									e.target.setCustomValidity(
										"     8  "
									)
								}
								className="sign-up-textbox w-75"
								placeholder="Student ID"
								value={studentID}
								autoComplete="username"
								required="required"
								onChange={(e) =>
									this.setState({
										studentID: e.target.value,
									})
								}
							/>
						</Form.Group>

						<Form.Group className="form-inline">
							<Form.Label className="w-25"></Form.Label>
							<Form.Control
								type="email"
								pattern=".{6,}"
								onInput={(e) => e.target.setCustomValidity("")}
								onInvalid={(e) =>
									e.target.setCustomValidity(
										"          6  "
									)
								}
								className="sign-up-textbox w-75"
								placeholder="E-mail"
								value={email}
								required="required"
								onChange={(e) =>
									this.setState({ email: e.target.value })
								}
							/>
						</Form.Group>

						<Form.Group className="form-inline">
							<Form.Label className="w-25"> </Form.Label>
							<Form.Control
								type="password"
								pattern=".{6,15}"
								onInput={(e) => e.target.setCustomValidity("")}
								onInvalid={(e) =>
									e.target.setCustomValidity(
										"    6    15   "
									)
								}
								className="sign-up-textbox w-75"
								placeholder="Password"
								value={password}
								autoComplete="new-password"
								required="required"
								onChange={(e) =>
									this.setState({
										password: e.target.value,
									})
								}
							/>
						</Form.Group>

						<Form.Group className="form-inline">
							<Form.Label className="w-25">
								  
							</Form.Label>
							<Form.Control
								type="password"
								className="sign-up-textbox w-75"
								placeholder="Confirm Password"
								value={confirmPassword}
								required="required"
								onChange={(event) =>
									this.checkConfirmPassword(event)
								}
							/>
						</Form.Group>

						<Button
							type="submit"
							className="btn btn-success btn-block mt-4">
							<i
								className="fa fa-user-plus px-2"
								aria-hidden="true"></i>
							 
						</Button>
					</Form>
				</Card.Body>
				<Card.Footer className="border-primary bg-transparent">
					        <ModalSignIn />  !
				</Card.Footer>
			</Card>
		);
	}
}

export default withRouter(SignUp);
