import { Form, Card, Row, Col, Button } from "react-bootstrap";
import { useState, useContext } from "react";
import { OK, Sorry } from "../../tools/notification";
import GlobalContext from "../../globals/state/GlobalContext";
import userServices from "../../services/http/userServices";
import Configs from "../../services/configs";
import LoadingBar from "../../commons/LoadingBar";

import "../profile.css";

const PasswordChange = () => {
	const [password, setPassword] = useState("");

    const [newPassword, setNewPassword] = useState("");
	const [confirmNewPassword, setConfirmNewPassword] = useState("");
	const context = useContext(GlobalContext);
	const [loading, setLoading] = useState("");

	const checkConfirmPassword = (event) => {
		setConfirmNewPassword(event.target.value);
		if (newPassword !== event.target.value) {
			event.target.setCustomValidity(
				"             "
			);
			//console.log(password, confirmPassword);
		} else event.target.setCustomValidity("");
	};


	const changeMyPassword = async (event) => {
		event.preventDefault();
        setLoading(true);
		try {
			const { status } = await userServices.changeMyPassword({
				password,
				newPassword,
			});
			if (status === Configs.Status.Successful) {
				OK("      ");
				context.signOut();
			}
		} catch (err) {
			if (!Configs.Status.isErrorExpected(err))
				Sorry(
					"      ...    "
				);
		}
		setPassword("");
		setLoading(false);
	};
	return (
		<Form onSubmit={e => changeMyPassword(e)}>
            <LoadingBar loading={loading} />
			<Form.Group className="form-inline">
				<Form.Label className="w-25 text-center">
					  
				</Form.Label>
				<Form.Control
					type="password"
					pattern=".{6,15}"
					onInput={(e) => e.target.setCustomValidity("")}
					onInvalid={(e) =>
						e.target.setCustomValidity(
							"    6    15   "
						)
					}
					className="account-info-textbox w-75 text-left"
					placeholder="New Password"
					value={newPassword}
					required="required"
					onChange={(e) => setNewPassword(e.target.value)}
				/>
			</Form.Group>
			<Form.Group className="form-inline">
				<Form.Label className="w-25 text-center">
					   
				</Form.Label>

				<Form.Control
					type="password"
					className="account-info-textbox w-75 text-left"
					placeholder="Confirm New Password"
					value={confirmNewPassword}
					required
					onChange={(event) => checkConfirmPassword(event)}
				/>
			</Form.Group>
			<Card.Footer className="p-1 m-0">
				<Row>
					<Col lg={2} md={2} sm={4} xs={4}>
						<Form.Label className="text-center w-100 mt-3">
							 
						</Form.Label>
					</Col>
					<Col lg={7} md={7} sm={8} xs={8}>
						<Form.Control
							type="password"
							className="text-left account-info-textbox animated-textbox"
							placeholder="Password"
							value={password}
							required
							onInput={(e) => e.target.setCustomValidity("")}
							onInvalid={(e) =>
								e.target.setCustomValidity(
									"               "
								)
							}
							onChange={(e) => setPassword(e.target.value)}
						/>
					</Col>
					<Col lg={3} md={3} sm={12} xs={12}>
						<Button
							type="submit"
							block
							variant="success"
							className="mt-2 animated-button">
							<i
								className="fa fa-key px-2"
								aria-hidden="true"></i>
							  
						</Button>
					</Col>
				</Row>
			</Card.Footer>
		</Form>
	);
};

export default PasswordChange;
