import { Form, Card, Row, Col, Button } from "react-bootstrap";
import Configs from "../../services/configs";
import userServices from "../../services/http/userServices";
import { useState, useEffect } from "react";
import { OK, Sorry } from "../../tools/notification";
import LoadingBar from "../../commons/LoadingBar";

import "../profile.css";

const Credentials = () => {
	const [fullname, setFullname] = useState("");
	const [studentID, setStudentID] = useState("");
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [loading, setLoading] = useState("");

	const [pageUpdateTrigger, triggerPageUpdate] = useState(false); // true <=> false -> triggers page , ==> see useEffect

	const reloadPage = () => {
		setLoading(false);
		triggerPageUpdate(!pageUpdateTrigger);
	};

	useEffect(() => {
		(async () => {
			try {
				setLoading(true);
				const { status, data } = await userServices.getMyCredentials();
				if (status === Configs.Status.Successful) {
					const { me } = data;
					setFullname(me.fullname);
					setStudentID(me.studentID);
					setEmail(me.email);
				}
			} catch (err) {
				if (!Configs.Status.isErrorExpected(err))
					Sorry(
						"      . ...    ."
					);
				setLoading(false);
			}
			setLoading(false);
		})();
	}, [pageUpdateTrigger]);

	const saveChanges = async (event) => {
		//add patern and stuff to states and inputs
		// check all inputs plz
		event.preventDefault();
		setLoading(true);
		try {
			const { status } = await userServices.editMyCredentials({
				studentID,
				fullname,
				email,
				password,
			});
			if (status === Configs.Status.Successful) {
				OK(`    `);
				// dispatch(TriggerRecordUpdate());
				// find another way to update

				reloadPage();
			}
		} catch (err) {
			if (!Configs.Status.isErrorExpected(err))
				Sorry(
					"      ...    "
				);
		}
		setPassword("");
		setLoading(false);
	};

	return (
		<Form onSubmit={(e) => saveChanges(e)}>
			<LoadingBar loading={loading} />

			<Form.Group className="form-inline">
				<Form.Label className="w-25 text-center">
					 
				</Form.Label>
				<Form.Control
					type="text"
					disabled
					className="account-info-textbox w-75"
					placeholder="Student ID"
					value={studentID}
				/>
			</Form.Group>
			<Form.Group className="form-inline">
				<Form.Label className="w-25 text-center"> </Form.Label>
				<Form.Control
					type="text"
					className="account-info-textbox w-75"
					pattern="[- ]{6,}" // persian characters and space
					onInput={(e) => e.target.setCustomValidity("")}
					onInvalid={(e) =>
						e.target.setCustomValidity(
							"            "
						)
					}
					placeholder="Full Name"
					value={fullname}
					required="required"
					onChange={(e) => setFullname(e.target.value)}
				/>
			</Form.Group>

			<Form.Group className="form-inline">
				<Form.Label className="w-25 text-center"></Form.Label>
				<Form.Control
					type="email"
					pattern=".{6,}"
					onInput={(e) => e.target.setCustomValidity("")}
					onInvalid={(e) =>
						e.target.setCustomValidity(
							"          6  "
						)
					}
					className="account-info-textbox w-75"
					placeholder="E-mail"
					value={email}
					required="required"
					onChange={(e) => setEmail(e.target.value)}
				/>
			</Form.Group>
			<Card.Footer className="p-1 m-0">
				<Row>
					<Col lg={2} md={2} sm={4} xs={4}>
						<Form.Label className="text-center w-100 mt-3">
							 
						</Form.Label>
					</Col>
					<Col lg={7} md={7} sm={8} xs={8}>
						<Form.Control
							type="password"
							className="text-left account-info-textbox animated-textbox"
							placeholder="Password"
							value={password}
							required
							onInput={(e) => e.target.setCustomValidity("")}
							onInvalid={(e) =>
								e.target.setCustomValidity(
									"               "
								)
							}
							onChange={(e) => setPassword(e.target.value)}
						/>
					</Col>
					<Col lg={3} md={3} sm={12} xs={12}>
						<Button
							type="submit"
							block
							variant="success"
							className="mt-2 animated-button">
							<i
								className="fa fa-floppy-o px-2"
								aria-hidden="true"></i>
							 
						</Button>
					</Col>
				</Row>
			</Card.Footer>
		</Form>
	);
};

export default Credentials;
